.search-form {
  margin-bottom: 10px;
  padding: 20px;
  background-color: $accentColor;
  text-align: center;
}

.search-form input {
  padding: 5px;
  border-radius: 5px;

  &:hover,
  &:focus {
    background-color: rgba(181, 245, 129, 0.712);
  }
}

button {
  padding: 5px;
  border-radius: 7px;

  background-color: rgb(248, 165, 40);

  transition: background-color $durationTimingFunction;

  &:hover,
  &:focus {
    background-color: rgb(246, 250, 0);
  }
}

.load-more {
  display: block;
  margin-top: 20px;
  margin-right: auto;
  margin-left: auto;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.photo-card {
  margin: 10px;
  box-shadow: 0px 3px 1px rgba(0, 0, 0, 0.1), 0px 1px 2px rgba(0, 0, 0, 0.08),
    0px 2px 2px rgba(0, 0, 0, 0.12);
  border-radius: 5px;
  overflow: hidden;
}

img {
  display: block;
  width: 360px;
  height: 240px;

  object-fit: cover;
  overflow: hidden;
}

.info {
  display: flex;
  justify-content: space-evenly;
  border-radius: 0 0 5px 5px;
}

.info-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}
